.primary {
    color: rgb(221, 78, 78);
    text-align: center;
}

select {
    text-align: center;
    padding: 0px 0px 0px 0px;
    width: 40%;
  }
  
body {
    background-color:  lightblue;
    text-align: center;
    padding: 0px 0px 0px 0px;
   

  }

label {
    text-align: center;
     
    padding: 0px;
}
  

  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  .container {
    padding: 0 1rem;
  
  }
  
  .btn {
    display: inline-block;
    border: none;
    background: #555;
    color: #fff;
    padding: 7px 20px;
    cursor: pointer;
  
  }
  
  .btn:hover {
    background: #666;
    
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  